body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.App {
  background-color: #f9f9f9;
}
.header-menu-logo-desktop {
  height: 60px;
  margin: 10px 40px;
  z-index: 9999;
}

.calc-header-menu {
  -webkit-filter: none !important;
  -moz-filter: none !important;
  -o-filter: none !important;
  -ms-filter: none !important;
  filter: none !important; 
}
.header-menu-logo-mobile {
  height: 40px;
}

.logo-link-container {
  margin: 10px auto;
  padding-right: 50px;
}
#blog-feed {
  padding: 4em 0em;
  background-color: #f9f9f9;
}

#blog-header {
  margin-bottom: 35px;
}
#blog-entry {
  padding: 4em 0em;
  background-color: #f9f9f9;
}

.article-img {
  max-width: 100%;
  margin-bottom: 20px;
  margin-top: -10px;
}
.expense_row{
  -webkit-transition: opacity .5s ease-in-out;
  transition: opacity .5s ease-in-out;
}

.loss-calc {
  min-height: 600px;
  -webkit-filter: blur(3px);
  -moz-filter: blur(3px);
  -o-filter: blur(3px);
  -ms-filter: blur(3px);
  filter: blur(3px);
  margin-top: 100px;
}

.loss-calc.mobile {
  min-height: 400px;
  -webkit-filter: none;
  -moz-filter: none;
  -o-filter: none;
  -ms-filter: none;
  filter: none;
}

.loss-calc-done {
  min-height: 600px;
}

.section-header {
  font-size: 3em !important;
  margin-bottom: 30px;
}

.section-header.mobile {
  font-size: 3em !important;
  margin-bottom: 30px;
}

#wrapper {
  height: 100%;
  width: 100%;
  margin-top: 30px
}

#meter{
  width: 100%;
  height: 100%;
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
}

#meter-header{
  left: 50%;
  position: absolute;
  top: 0;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
}

.outline, #mask {
  stroke: #F1F1F1;
  stroke-width: 32;
  -webkit-transition: 3s ease-in-out;
  transition: 3s ease-in-out;
}
.range {
  stroke-width: 60;
}
#slider, #lbl {
  position: absolute;
}
#slider {
  cursor: pointer;
  left: 0;
  margin: auto;
  right: 0;
  top: 70%;
  width: 94%;
  display: none;
}
#lbl {
  background-color: #4B4C51;
  border-radius: 2px;
  color: white;
  font-family: 'courier new';
  font-size: 25pt;
  font-weight: bold;
  padding: 4px 4px 2px 4px;
  width: 90px;
  right: calc(50% - 45px);
  top: 80%;
}
#lbl.mobile {
  width: 50px;
  font-size: 18pt;
  right: calc(50% - 25px);
  top: 70%;
}
#needle {
  height: 43%;
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  top: 24%;
  -webkit-transform-origin: 50% 90%;
          transform-origin: 50% 90%;
  /*orientation fix*/
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
  -webkit-transition: 3s ease-in-out;
  transition: 3s ease-in-out;
}
.modal {
  text-align: center !important;
}

.modal.mobile {
  margin-top: -82px !important;
}

.modal-header {
  height: 90px;
  padding-top: 10px !important;
}

.modal-header.mobile {
  height: 75px;
  padding: 10px !important;
}

.ui.progress {
  margin-top: 10px !important;
}


.ui.progress.mobile {
  margin-top: -5px !important;
}

.modal-initial-heading {
  font-size: .8em;
  margin-top: 18px !important;
}

.modal-progress-container {
  font-size: .7em !important;
  font-weight: normal !important;
  text-align: center;
}

.modal-progress-bar-completed {
  background-color: #008066 !important;
}

.modal-content {
  min-height: 450px;
  position: relative;
  vertical-align: middle;
}

.modal-content.mobile {
  min-height: 350px;
}

.modal-description {
  left: 50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  max-width: 90% !important;
  height: 100%;
  min-width: 350px !important;
}

.modal-description.mobile {
  /*width: 90% !important;*/
  width: 300px !important;
}

.modal-question {
  display: block;
  font-size: 1.5em;
  margin-bottom: 60px;
  margin-top: 60px;
}
.modal-question.mobile {
  margin-top: 0px;
  margin-bottom: 15px;
}

.modal-input-container {
  left: 50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  /*width: 100%;*/
}
.modal-input-container.mobile {
  width: 90%;
}


.modal-question-description {
  margin-top: 30px;
}

.progress-button-container {
  text-align: center;
  position: absolute;
  left: calc(50% - 120px);
  bottom: 15px;
}

.progress-button-container.mobile {
  bottom: 10px;
}
.back-button-spacer {
  width: 55px;
  height: 5px;
  display: inline-block;
}

.back-button {
  margin: 10px !important;
  width: 110px;
}

.back-button.mobile {
  width: 100px;
}

.next-button.mobile {
  width: 100px;
}

.next-button {
  background-color: #008066 !important;
  color: white !important;
  margin: 10px !important;
  width: 110px; 
}

.ui.dimmer {
  top: 82px !important;
}

.ui.dimmer.mobile {
  top: 60px !important;
}

input[type="number"]::-webkit-outer-spin-button, input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
 
input[type="number"] {
    -moz-appearance: textfield;
}
.result-container {
  width: 100% !important;
  height: 100% !important;
  margin: 0 !important;
  position: absolute;
  font-family: PlayfairDisplay-Black;
}

.page-container {
  padding: 30px;
  height: 1120px;
  padding-top: 70px;
  position: relative;
}

.heading {
  font-size: 36pt;
  margin: 300px auto 400px auto !important;
  text-align: center;
  font-family: PlayfairDisplay-Black;
  font-weight: normal;
}

.heading-logo {
  width: 150px;
  margin: 30px auto;
  display: block;
}

.heading-signature {
  text-align: center;
  font-size: 18pt;
  margin-bottom: 120px !important;
}

.page-footer {
  text-align: center;
  position: absolute;
  left: calc(50% - 322px);
  bottom: 10px;
}

.item-heading {
  margin-top: 50px;
  /*font-size: 20pt;*/
}

.service-list {
  list-style: none;
  font-size: 14pt;
}

.service-list-text {
  font-size: 11pt;
  margin-top: 6px;
  margin-bottom: 12px !important;
}

.service-list-item:before {
  content: "";  
  border-color: transparent #008066;  
  border-style: solid;  
  border-width: 0.35em 0 0.35em 0.45em;  
  display: block;  
  height: 0;  
  width: 0;  
  left: -1em;  
  top: 0.9em;  
  position: relative;
}

.heading-logo-desktop {
  height: 220px;
  padding: 10px;
  margin: -150px auto 40px auto;
}

.heading-logo-mobile {
  height: 110px;
  padding: 10px;
  margin: -110px auto 30px auto;
}
